	module.exports = {"data":{"markdownRemark":{"html":"<p>I hate not keeping to my blog schedule, so I decided to post a quick update.</p>\n<h2>New Assignment</h2>\n<p>First the big news, I got a new job!!! I’m not an instructional designer at the University of Texas. I’m super excited to be working on some really innovative projects with my new team. AND it looks like one of my first assignments will be to create a dataviz using the D3 JavaScript library so that is going to be a great way to continue building on my programming skills.</p>\n<h2>A Quick Side Project</h2>\n<p>I’m also participating in Gumroad’s Small Product Lab, a 10-day sprint to ship a small product with the goal of getting at least 1 person to give you $1 for something that you made. It’s going to be a bit tough since I don’t have my desktop available (I’m still transient, currently) but I think I can still make it through. Looking forward to that.</p>\n<h2>October GDS Challenge?</h2>\n<p>I’m also gearing up to revamp Go Design Something once again, this time as a weekly podcast. The one thing I do know is that I love making things, I love having side projects every month and I think that this has been such a transformational process for me that I really want to share it with other LX and instructional designers. I think the most important thing for me at this point will be to remain consistent and keep it about the work. So hoping to jump on that in October, once I get settled.</p>","frontmatter":{"date":"September 29, 2015","path":"/blog/new-job-new-city","title":"New Job, New City","tags":["JavaScript","job","show your work","University of Texas","UT","work"],"categories":["GDS Challenge","Showing my Work"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/IMG_0426-0-b01c421402b12cd2cfcfcedb42104715-81d39.png","srcSet":"/static/IMG_0426-0-b01c421402b12cd2cfcfcedb42104715-9a8d1.png 203w,\n/static/IMG_0426-0-b01c421402b12cd2cfcfcedb42104715-c53f9.png 405w,\n/static/IMG_0426-0-b01c421402b12cd2cfcfcedb42104715-81d39.png 810w,\n/static/IMG_0426-0-b01c421402b12cd2cfcfcedb42104715-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

